<template>
  <div>
    <el-row
      :gutter="10"
      v-loading="templateStore.loading"
      element-loading-text="Cargando..."
      type="flex"
      justify="center"
    >
      <el-col :xs="23" :sm="23" :md="12" :lg="12" :xl="12">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>Agrega archivos a tu sitio</span>
          </div>

          <div class="text item">
            <label>Arrastra tu archivo css para agregarlo a tu plantilla</label
            ><br />

            <!-- Uploader -->
            <el-upload
              name="fileCSS"
              action="#"
              class="upload-demo"
              :show-file-list="false"
              :http-request="uploadFile"
              drag
            >
              <i class="el-icon-upload"></i>
              <div class="el-upload__text">
                Suelta tu archivo aquí o <em>haz clic para cargar</em>
              </div>
              <div slot="tip" class="el-upload__tip">
                Solo archivos CSS con un tamaño menor de 10 MB
              </div>
            </el-upload>
            <!-- End Uploader -->
          </div>

          <el-divider></el-divider>

          <div>
            <!-- Add CDN -->
            <label>Agrega url CDN par agregarla al sitio</label><br /><br />
            <el-row :gutter="10">
              <el-col :span="19">
                <el-input placeholder="Url CDN" v-model="cdnUrl"></el-input>
              </el-col>
              <el-col :span="2">
                <el-button type="success" @click="uploadCDN">
                  Agregar CDN
                </el-button>
              </el-col>
            </el-row>
          </div>
          <!-- End CDN -->
        </el-card>
      </el-col>

      <el-col :xs="23" :sm="23" :md="11" :lg="11" :xl="11">
        <el-card class="box-card">
          <div slot="header" class="clearfix">
            <span>Archivos cargados en el sitio</span>
          </div>
          <div class="text item">
            <div class="custom-tree-container">
              <div class="block">
                <!-- Start Tree -->
                <el-tree
                  :data="cssStore.list"
                  node-key="id"
                  :props="defaultProps"
                  default-expand-all
                  @node-drop="handleDrop"
                  draggable
                  :allow-drop="allowDrop"
                >
                  <span class="custom-tree-node" slot-scope="{ node, data }">
                    <span>{{ node.label }}</span>
                    <span>
                      <el-button
                        type="text"
                        size="mini"
                        @click="() => deleteFile(node, data)"
                      >
                        Eliminar
                      </el-button>
                    </span>
                  </span> </el-tree
                ><br />
                <!-- End Tree -->
                <div align="right">
                  <el-button
                    type="primary"
                    size="medium"
                    aling="right"
                    @click="updateTree()"
                  >
                    Actualizar
                  </el-button>
                </div>
              </div>
            </div>
          </div>
        </el-card>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import { useStoreCss } from '../../../stores/template/files/cssStore'
import { useStoreTemplate } from '../../../stores/template/templateStore'
import { upload } from '@/api/utilities/files/css'
import _ from 'lodash'

// import { codemirror } from 'vue-codemirror'

const cssStore = useStoreCss()
const templateStore = useStoreTemplate()

export default {
  name: 'Css',
  components: {
    // codemirror
  },
  data () {
    return {
      cssStore,
      templateStore,
      cdnUrl: '',
      defaultProps: {
        label: 'name'
      },
      content: '',
      cmOptions: {
        tabSize: 4,
        mode: 'text/css',
        theme: 'dracula',
        lineNumbers: true,
        line: true
      }
    }
  },
  created () {
    // cssStore.fetchCss()
    // cssStore.fetchCustomCss()
  },
  mounted () {},
  computed: {
    codemirror () {
      return this.$refs.editorCss.codemirror
    }
  },
  methods: {
    async uploadCDN () {
      templateStore.loading = true

      cssStore.file.name = this.cdnUrl
      cssStore.file.pathFile = this.cdnUrl
      cssStore.file.type = 'External'
      cssStore.file.order = cssStore.list.length + 1

      const fileData = _.cloneDeep(cssStore.file)
      const keyName = _.cloneDeep(templateStore.keyName)

      cssStore.storeCssFile(fileData, keyName).finally(() => {
        this.cdnUrl = ''
        templateStore.loading = false
      })
    },
    async uploadFile (param) {
      templateStore.loading = true
      const formData = new FormData()
      formData.append('file', param.file)
      formData.append('fileName', param.file.name)
      formData.append('folder', 'template/' + templateStore.keyName)
      upload(formData)
        .then((response) => {
          cssStore.file.name = param.file.name
          cssStore.file.pathFile = response.data
          cssStore.file.type = 'Internal'
          cssStore.file.order = cssStore.list.length + 1
          const fileData = _.cloneDeep(cssStore.file)
          const keyName = _.cloneDeep(templateStore.keyName)
          cssStore.storeCssFile(fileData, keyName)
        })
        .catch((response) => {
          // console.log(response)
          param.onError()
        })
        .finally(() => (templateStore.loading = false))
    },
    handleDrop () {
      cssStore.orderFilesCss()
    },
    allowDrop (draggingNode, dropNode, type) {
      if (type !== 'inner') {
        return true
      }
    },
    updateTree () {
      templateStore.loading = true
      const keyName = _.cloneDeep(templateStore.keyName)
      const listData = _.cloneDeep(cssStore.list)

      cssStore
        .updateCssFiles(listData, keyName)
        .finally(() => (templateStore.loading = false))
    },
    deleteFile (node, data) {
      this.$confirm(
        'Se borrara el arvhivo de su sitio, ¿Esta seguro?',
        'Cuidado',
        {
          confirmButtonText: 'OK',
          cancelButtonText: 'Cancelar',
          type: 'warning'
        }
      )
        .then(() => {
          templateStore.loading = true
          const parent = node.parent
          const children = parent.data.children || parent.data
          const index = children.findIndex((d) => d._id === data._id)
          const keyName = _.cloneDeep(templateStore.keyName)
          const id = _.cloneDeep(data._id)
          cssStore
            .deleteCssFile(keyName, id)
            .then((response) => {
              children.splice(index, 1)
            })
            .finally(() => (templateStore.loading = false))
        })
        .catch(() => {
          this.$message({
            showClose: true,
            type: 'info',
            message: 'Se cancelo la operación'
          })
        })
    },
    saveCustomCss () {
      const keyName = _.cloneDeep(templateStore.keyName)
      const customData = _.cloneDeep(cssStore.content)

      cssStore.updateCustomCss(keyName, customData)
    }
  }
}
</script>

<style>
.custom-tree-node {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1em;
  padding-right: 8px;
}
</style>
